import React from 'react';
import './RadioButtons.scss';

interface Props {
  onTypeOfChart: (value: string) => void;
  typeOfChart: string;
}

export const RadioButtons = React.memo<Props>(({
  onTypeOfChart,
  typeOfChart,
}) => {
  return (
    <div className="radioButtons">
      <label 
        className="radioButtons--cursor"
        htmlFor="BarChart"
      >
        Bar chart
        <input
          className="radioButtons--cursor"
          type="radio"
          name="BarChart"
          id="BarChart"
          value="bar"
          checked={typeOfChart === 'bar'}
          onChange={(event) => onTypeOfChart(event.target.value)}
        />
      </label>
      <label 
        className="radioButtons--cursor"
        htmlFor="LineChart"
      >
        Line chart
        <input
          className="radioButtons--cursor"
          type="radio"
          name="LineChart"
          id="LineChart"
          value="line"
          checked={typeOfChart === 'line'}
          onChange={(event) => onTypeOfChart(event.target.value)}
        />
      </label>
      <label 
        htmlFor="RadarChart"
        className="radioButtons--cursor"
      >
        PolarArea chart
        <input
          className="radioButtons--cursor"
          type="radio"
          name="PolarArea"
          id="PolarArea"
          value="polarArea"
          checked={typeOfChart === 'polarArea'}
          onChange={(event) => onTypeOfChart(event.target.value)}
        />
      </label>
      <label 
        htmlFor="RadarChart"
        className="radioButtons--cursor"
      >
        Radar chart
        <input
          className="radioButtons--cursor"
          type="radio"
          name="RadarChart"
          id="RadarChart"
          value="radar"
          checked={typeOfChart === 'radar'}
          onChange={(event) => onTypeOfChart(event.target.value)}
        />
      </label>
      <label 
        htmlFor="DoughnutChart"
        className="radioButtons--cursor"
      >
        Doughnut chart
        <input
          className="radioButtons--cursor"
          type="radio"
          name="DoughnutChart"
          id="DoughnutChart"
          value="doughnut"
          checked={typeOfChart === 'doughnut'}
          onChange={(event) => onTypeOfChart(event.target.value)}
        />
      </label>
    </div>
  );
});
